.card {
    background-color: white;
    border-radius: 20px;
    height: 70px;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    text-align: center;
    cursor: pointer;
    padding: 12px;
    transition: transform 0.5s ease-in-out,background-color 1s ease-in-out,color 1s ease-in-out;
}

.card-content {
    padding: 15px;
}

.card:hover{
    transform: scale(1.05);
    background-color: rgb(2, 2, 44);
    color: white !important;
}

.card-image {
    width: 50%;
    height: 50%;
}
.card-link{
    font-weight: 800;
    color: blue;
}

.card-content h5{
    margin: 0;
    font-weight:400;
    color: rgb(3, 3, 20);
    font-size:  15px !important;
    text-transform: capitalize;
}
.card-content:hover {
    &>h5 {
        color: white;
    }
}

p {
    font-size: 12px;
}
