.modal {
    font-size: 12px;
    background-color: rgba(2, 2, 2, 0.671);
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;

  .modal-content {
      position: relative;
      overflow-y: scroll;
      display: flex;
      flex-direction: column;
      align-items: center;
    background-color: green !important;
    width: 80%;
    height:200px;
    padding: 12px;
    color: white;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;

    .control-font-size {
        position: fixed;
        top: 48vh;
        left: 150px;
    }

    h5 {
        font-size: 25px;
        text-transform: uppercase;
        font-weight: 800;
        padding: 0;
        margin-bottom: 0;
        padding-bottom: 0;  
    }

    h5::after{
        margin: 5px auto;
        content: '';
        display: block;
        width: 130px;
        border: 1px solid white;
    }

    hr {
        margin-bottom: 25px;
    }

    div {
        
        font-weight: bold;
    }
  }
  
}